import React from 'react';
import { Collapse } from 'antd';
import {Tasks} from './Tasks';


const Panel = Collapse.Panel;



export const TaskContainer = ({name, tasks, description, handleText, _id, onSubmit, projectID}) =>(

    <Collapse accordion>
        <Panel header={name} key="1">
            <p>{description}</p>
            <Tasks tasks={tasks} data={_id} handleText={handleText} onSubmit={onSubmit} projectID = {projectID}/>
        </Panel>
    </Collapse>
);