import React, { Component } from 'react';
import { Steps, Button, message } from 'antd';
import TeamForm from "./TeamForm";
import UserForm from "./UserForm";
import axios from "axios";
import {base_url} from "../Services/base_url";
import { Redirect } from 'react-router-dom';

const Step = Steps.Step;

class SignupCompany extends Component {
    constructor(props) {
        super(props);
        this.state = {
            current: 0,
            tags: [],
            inputVisible: false,
            inputValue: "",
            data: {},
            user: {},
            company: {
                _id:""
            },
            teams: [

            ],
            selectedTeam: ""
        };
    }

//TeamForm

    handleClose = (removedTag) => {
        const tags = this.state.tags.filter(tag => tag !== removedTag);
        console.log(tags);
        this.setState({ tags });
    };

    handleInputChange = (e) => {
        this.setState({ inputValue: e.target.value });
    };

    handleInputConfirm = () => {
        const state = this.state;

        const inputValue = state.inputValue;
        let tags = state.tags;
        let data = state.data;
        if (inputValue && tags.indexOf(inputValue) === -1) {
            //team = { name:inputValue, company:company._id};
            //tags.push(team);
            tags= [...tags, inputValue];
        }
        data['teams']=tags;
        console.log(tags);
        this.setState({
            tags,
            inputVisible: false,
            inputValue: '',
            data
        });
    };
// Termina TeamForm

//Steps
    next() {
        let {data} = this.state;
        const current = this.state.current + 1;
        this.setState({ current });
        if(current === 1) {
            axios.post(`${base_url}api/company`, data)
                .then(res => {
                    console.log("Correcto", res);
                    const company = res.data.newCompany;
                    this.setState({data:{},company})

                })
                .catch(err => {
                    console.error(err);
                })
        }
        if(current === 2) {
            let {tags, company} = this.state;
            let  teams = [];
            tags.forEach((tag) => {
                teams.push({name:tag, _company:company._id});
            });
            axios.post(`${base_url}api/team/many`, teams)
                .then(res => {
                    console.log("Correcto", res);
                    const teams = res.data.newTeams;
                    this.setState({data:{},teams})

                })
                .catch(err => {
                    console.error(err);
                })

        }
    }

    prev() {
        const current = this.state.current - 1;
        this.setState({ current });
    }
//Termina Steps

    handleText = (e) => {
        let{data} = this.state;
        let field = e.target.name;
        data[field]= e.target.value;
        this.setState({data});
    };

/// usuario


    handleSubmit  (){
        let {data, company} = this.state;
        if(data.password !== data.confirmPassword) {
            message.error('Las contraseñas no coinciden');
        } else {
            message.success('Processing complete!');
            data._company = company._id;
            axios.post(`${base_url}api/auth/signup`, data)
                .then(res => {
                    console.log("Correcto", res);
                    const user = res.data.newUser;
                    this.setState({data:{}, user});

                    return <Redirect to="/"/>
                })
                .catch(err => {
                    console.error(err);
                })
        }


    }

    render(){
        const { current } = this.state;
        const steps = [
            {title: "Registro de empresa",
                content:
                    <form action="POST">
                        <div>
                            <label htmlFor="">
                                Nombre de la empresa:
                                <input required className="uk-input" onChange={this.handleText} name="name" type="text"/>
                            </label>
                        </div>
                    </form>
            },

            {title: "Registro de equipos",
                content: <TeamForm
                    tags={this.state.tags}
                    handleInputConfirm={this.handleInputConfirm}
                    handleInputChange={this.handleInputChange}
                    inputValue={this.state.inputValue}
                    handleClose={this.handleClose}
                />
            },

            {title: "Registro de usuario",
                content: <UserForm handleText={this.handleText} selectedTeam={this.state.selectedTeam} teams={this.state.teams} company={this.state.company}/>},

        ];
        return (
            <div className="center-div forms" >
                <Steps current={current}>
                    {steps.map(item => <Step key={item.title} title={item.title} />)}
                </Steps>
                <div className="steps-content">{steps[current].content}</div>
                <div>
                    <div className="steps-action">
                        {
                            current < steps.length - 1
                            && <Button type="primary" onClick={() => this.next()}>Next</Button>
                        }
                        {
                            current === steps.length - 1
                            && <Button type="primary" onClick={() => this.handleSubmit ()}>Done</Button>
                        }
                        {
                            current > 0
                            && (
                                <Button style={{ marginLeft: 8 }} onClick={() => this.prev()}>
                                    Previous
                                </Button>
                            )
                        }
                    </div>
                </div>

            </div>

        )
    }
}

export default SignupCompany;