import React, { Component } from 'react';
import {Form, Select} from 'antd'
class UserForm extends Component {



    render(){
        let {teams,handleText} = this.props;

        return(
            <Form action="POST">
                    <label htmlFor="">
                        Nombre:
                        <input name="name" type="text" onChange={handleText}/>
                    </label>
                    <label htmlFor="">
                        Apellido:
                        <input name="lastname" type="text" onChange={handleText}/>
                    </label>
                    <label htmlFor="">
                        Usuario:
                        <input name="username" type="text" onChange={handleText}/>
                    </label>
                    <label htmlFor="">
                        Email:
                        <input name="email" type="email" onChange={handleText}/>
                    </label>
                    <label htmlFor="">
                        Contraseña:
                        <input name="password" type="password" onChange={handleText}/>
                    </label>
                    <label htmlFor="">
                        Confirmar contraseña:
                        <input name="confirmPassword" type="password" onChange={handleText}/>
                    </label>
                    <label htmlFor="">
                        Equipo:
                        <Select name="_team"  onChange={handleText} >
                            <option value={0}>Seleciona un equipo</option>
                            {teams.map((data, i)=>
                            <option value={data._id} key={i}>{data.name}</option>
                            )}
                        </Select>
                    </label>
                    <label htmlFor="">
                        Tipo de usuario:
                        <Select name="role" onChange={handleText}>
                            <option value="Selecciona">Selecciona uno</option>
                            <option value="Administrador">Administrador</option>
                            <option value="Líder de equipo">Líder de equipo</option>
                            <option value="Miembro de equipo">Miembro de equipo</option>
                        </Select>
                    </label>
            </Form>
        )
    }
}

export default UserForm;