import React from 'react';
import {Checkbox, Input,Button} from 'antd';



function onChange(e) {
    console.log(`checked = ${e.target.checked}`);
}



export const Tasks = ({tasks, data, handleText, onSubmit, projectID}) =>(
    <div>
        <Input name="name" onChange={handleText}/>
        <Button type="primary" onClick={()=>onSubmit(data)} >Agregar Tarea</Button>
        {projectID?console.log('project',projectID):null}
        {tasks ? tasks
            .filter(item => item._project === projectID)
            .map((d,i) => {
                console.log('task',d);
            return <Checkbox key={i} value={d._id} onChange={onChange}>{d.name}</Checkbox>
            }

        ):""}



    </div>



);