import React, {Component} from 'react';
import { Modal, Button, Form } from 'antd';
import axios from "axios";
import {base_url} from "../Services/base_url";
import {addtask, getTask, getAllTask} from "../Services/TaskService";
import {TaskContainer} from "./TaskContainer";
import {projectsService} from "../Services/projects";

class Project extends Component {

    constructor() {
        super();
        this.state = {
            visible: false,
            user: {

            },
            data: {},
            projects: [],
            tasks: []
        };
    }
    componentWillMount(){
            const user = JSON.parse(localStorage.getItem(('user')));
            let {projects, tasks} = this.state;
            if(user){
                this.setState({user})
            }
            projectsService()
                .then(res => {

                    projects=res.data.projects;
                    this.setState({projects})
                })
                .catch(err => {
                    console.log(err)
                });

            getTask()
                .then(res => {
                    console.log(res.data.tasks);
                    tasks=res.data.tasks;
                    this.setState({tasks})
                })
                .catch(err => {
                    console.log(err)
                })

            getAllTask().then(res => {
                let {tasks} = this.state;
                res.data.forEach(item => {
                    tasks.push(item);
                })
                this.setState({tasks})
            })
    }

        showModal = () => {
            this.setState({
                visible: true,
            });
        };

        handleOk = (e) => {
            console.log(e);
            let {data, user, projects} = this.state;
            this.setState({
                visible: false,
            });
            data._creator = user._id;
            console.log(data);
           axios.post(`${base_url}api/project`, data)
                .then(res => {
                    console.log("Correcto", res);
                    const project = res.data.newProject;
                    projects.push(project);
                    this.setState({data:{}, projects});
                })
                .catch(err => {
                    console.error(err);
                })
        };

        handleText = (e) => {
            let{data} = this.state;
            let field = e.target.name;
            data[field]= e.target.value;
            this.setState({data});
            console.log(data)
        };

        handleCancel = (e) => {
            console.log(e);
            this.setState({
                visible: false,
            });
        };


        onSubmit = (d) =>{
            let {data, tasks} =this.state;
            data["_project"]=d;
            addtask(data).then(r=>{
                console.log("exito",r);
                const task = r.data.newTask;
                tasks.push(task);
                this.setState({data:{}, tasks});
            }).catch(err=>
                {console.log(err)}
            )

        };


    render(){
            let{user, projects, tasks}=this.state;
        return(
            <div>

                <Button type="primary" onClick={this.showModal}>
                Agregar Prioridad
                </Button>
                <Modal
                title="Basic Modal"
                visible={this.state.visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                >
                    <Form action="">
                        <label htmlFor="">
                            Nombre:
                            <input onChange={this.handleText} name="name" type="text"/>
                        </label>
                        <label htmlFor="">
                            Encargado:
                            <p>{user.name}</p>
                        </label>
                        <label htmlFor="">
                            Descripción:
                            <textarea onChange={this.handleText} name="description"> </textarea>
                        </label>
                    </Form>
                </Modal>
                {projects.map((data, i) =>
                    <TaskContainer {...data} projectID = {data._id} tasks={tasks} handleText={this.handleText} value={data._id} key={i} onSubmit={this.onSubmit}/>
                    )}
                </div>

                );
            }

}

export default Project;