import React, { Component } from 'react';
import {Button} from 'antd'
import { login } from '../Services/service';

class Login extends Component {

    constructor(){
        super();
        this.state =  {
            user: {
                email: "",
                password: "",
            }
        }
    }

    handleChange = (e) => {
        const {user} = this.state;
        let field = e.target.name;
        user[field] = e.target.value;
        this.setState({user});
    };

    handleSubmit = (e) => {
        e.preventDefault();
        login(this.state.user, this.props.history)
    };

    render(){
        return(
            <form >
                <label htmlFor="">
                    Email:
                    <input name="email" onChange={this.handleChange} type="email"/>
                </label>
                <label htmlFor="">
                    Contraseña:
                    <input name="password" onChange={this.handleChange} type="password"/>
                </label>
                <Button onClick={this.handleSubmit}>Iniciar Sesión</Button>
            </form>
        )
    }

}

export default Login;

